.list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  list-style: none;
  margin: 20px auto 0;
  padding: 0;
}

.item {
  width: 200px;
  margin: 0 auto 20px;
}

.imageWrapper {
  max-width: 100%;
  height: 300px;
  background-color: #fff;
  background-image: url('../../assets/profile.jpg');
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.image {
  display: block;
  max-width: 100%;
  object-fit: cover;
  align-items: center;
}
